.customModule {
  width: 300px;
  height: 200px;
  background-color: #70ad47;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
}
.customModule__input-container {
  background-color: #70ad47;
  border: 2px solid #507e32;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.customModule__input-container--selected{
  border-top-color: red;
  border-left-color: red;
  border-bottom-color: red;

}
.customModule__input {
  display: flex;
  justify-content: center;
  align-items: center;
}
.customModule__input-p {
  font-size: 1.1rem;
  text-transform: uppercase;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -200%);
  text-align: center;
}
.customModule__input-img {
  width: 80%;
}
.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding-top: 5%;
  padding-bottom: 5%;
  background-color: #4472c4;
  border-top: 2px solid #2f528f;
  border-bottom: 2px solid #2f528f;
}
.info--selected{
  border-top-color: red;
  border-bottom-color: red;
}
.circle-container {
  width: 50%;
  display: flex;
  justify-content: space-between;
}
.circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
}
.circle--orange {
  background-color: #ffc000;
  border: 2px solid #bc8c00;
}
.circle--red {
  background-color: #ab5f2f;
  border: 2px solid #ae5a21;
}
.circle--green {
  background-color: #70ad47;
  border: 2px solid #507e32;
}
.circle--blue {
  position: absolute;
  right: 0;
  background-color: #4472c4;
  border: 2px solid #2f528f;
  transform: translate(50%);
  z-index: -1;
}
.circle--blue-input {
  left: 0;
  transform: translate(-50%);
}
.info__text {
  font-size: 1.4rem;
  flex-basis: 20%;
  width: 100%;
  background-color: #a5a5a5;
  border-top: 2px solid #787878;
  border-bottom: 2px solid #787878;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.customModule__output-container {
  background-color: #70ad47;
  border: 2px solid #507e32;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.customModule__output-container--selected{
  border-top-color: red;
  border-right-color: red;
  border-bottom-color: red;
}

.customModule__output {
  display: flex;
  justify-content: center;
  align-items: center;
}
.customModule__output-p {
  font-size: 1.1rem;
  width: 80%;
  text-transform: uppercase;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -200%);
  text-align: center;
}
.customModule__output-img {
  width: 80%;
}
